<script>
  import axios from 'axios';
  import VirtualList from '../components/SvelteVirtualList.svelte';

  import Map from '../components/Map.svelte';
  import ApartmentCard from '../components/ApartmentCard.svelte';
  import Modal from '../components/Modal.svelte';
  import Range from '../components/Range.svelte';

  let apartments = [];

  $: apartmentsFloor = apartments.map(a => (a.floor ? a.floor : 0));
  $: apartmentsPrice = apartments.map(a => a.price);
  $: apartmentsMPrice = apartments.map(a => a.m_price);
  $: apartmentsFloorRatio = apartments.map(a => a.floor_ratio);

  let filters = {};
  $: filteredApartments = filterApartments(apartments, filters);

  let visibleApartments = [];
  let showModal = false;

  let top = 200;

  const apartmentsPromise = axios('/api/apartments.json').then(res => {
    const aprtmnts = res.data.map(apartment => ({
      ...apartment,
      m_price:
        apartment.area_total === 0 ? 0 : apartment.price / apartment.area_total
    }));

    apartments = aprtmnts;
    visibleApartments = aprtmnts;
  });

  function filterApartments(apartments, filters) {
    return apartments.filter(apartment => {
      if (filters.floor) {
        const [minFloor, maxFloor] = filters.floor;
        if (apartment.floor < minFloor || apartment.floor > maxFloor) {
          return false;
        }
      }

      if (filters.price) {
        const [minPrice, maxPrice] = filters.price;
        if (apartment.price < minPrice || apartment.price > maxPrice) {
          return false;
        }
      }

      if (filters.m_price) {
        const [minMPrice, maxMPrice] = filters.m_price;
        if (apartment.m_price < minMPrice || apartment.m_price > maxMPrice) {
          return false;
        }
      }

      if (filters.floor_ratio) {
        const [minFloorRatio, maxFloorRatio] = filters.floor_ratio;
        if (
          apartment.floor_ratio < minFloorRatio ||
          apartment.floor_ratio > maxFloorRatio
        ) {
          return false;
        }
      }

      return true;
    });
  }

  function handleVisibleChange(event) {
    const startTime = Date.now();
    const set = new Set(event.detail.map(feature => feature.properties.url));
    visibleApartments = filteredApartments.filter(apartment =>
      set.has(apartment.url)
    );
    console.log(
      `Filtering ${filteredApartments.length} in ${Date.now() - startTime}ms`
    );
  }

  function handleModalShow() {
    showModal = true;
  }

  function handleModalClose() {
    showModal = false;
  }

  function handleFilterSubmit() {
    console.log('kek', filters);
    filters = {
      ...filters
    };
  }
</script>

<style>
  @media (max-width: 640px) {
    .list-container {
      top: 40vh;
    }
  }

  .filter-btn {
    position: absolute;
    left: 35px;
    top: 5px;
    z-index: 1;
  }
</style>

{#await apartmentsPromise}
  Loading...
{:then res}
  {#if showModal}
    <Modal on:close={handleModalClose} on:submit={handleFilterSubmit}>
      <h2>floor</h2>
      <Range
        values={apartmentsFloor}
        on:change={({ detail }) => (filters.floor = detail)}
      />

      <h2>price</h2>
      <Range
        values={apartmentsPrice}
        bins={50}
        on:change={({ detail }) => (filters.price = detail)}
      />

      <h2>m_price</h2>
      <Range
        values={apartmentsMPrice}
        on:change={({ detail }) => (filters.m_price = detail)}
      />

      <h2>floor_ratio</h2>
      <Range
        values={apartmentsFloorRatio}
        on:change={({ detail }) => (filters.floor_ratio = detail)}
      />
    </Modal>
  {/if}
  <div class="sm:flex">
    <div class="map-container w-full h-screen lg:w-1/2">
      <Map
        apartments={filteredApartments}
        on:visibleChange={handleVisibleChange}
      >
        <button
          on:click={handleModalShow}
          class="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4
          rounded filter-btn"
        >
          Filter
        </button>
      </Map>
    </div>
    <div
      class="list-container overflow-auto absolute w-full h-screen sm:relative
      bg-gray-100 lg:w-1/2"
      style="top: {top}px"
    >
      <div
        style="top: 0; left: 0; right: 0; height: 15px; background-color:
        #d0d0d0;"
        on:dragstart={() => console.log('Dragenter')}
        on:dragend={() => console.log('Dragend')}
        on:touchstart={() => console.log('Touchstart')}
        on:touchmove={e => {
          const { pageX, pageY } = e.targetTouches[0];
          if (pageY > 0) {
            top = pageY;
          }
          console.log(pageY);
        }}
        on:touchend={() => console.log('Touchend')}
      >
        <hr style="padding: 7px 5px;" />
      </div>
      <VirtualList items={visibleApartments} let:item>
        <ApartmentCard {...item} />
      </VirtualList>
    </div>
  </div>
{:catch err}
  <h1>{err.message}</h1>
{/await}
