<script>
  import { createEventDispatcher } from 'svelte';

  import CloseIcon from './icons/Close.svelte';

  const dispatch = createEventDispatcher();

  function handleClose() {
    console.log('handleClose');
    dispatch('close');
  }

  function handleKeydown(event) {
    if (event.keyCode === 27) {
      handleClose();
    }
  }

  function handleSubmit() {
    dispatch('submit');
  }
</script>

<style>
  .modal {
    z-index: 1;
    transition: opacity 0.25s ease;
  }

  :global(.modal-active) {
    overflow-x: hidden;
    overflow-y: visible !important;
  }
</style>

<svelte:window on:keydown={handleKeydown} />

<div
  class="modal fixed w-full h-full top-0 left-0 flex items-center justify-center">
  <div class="modal-overlay absolute w-full h-full bg-gray-900 opacity-50" />

  <div
    class="modal-container bg-white w-11/12 md:max-w-md mx-auto rounded
    shadow-lg z-50 overflow-y-auto">

    <div
      class="modal-close absolute top-0 right-0 cursor-pointer flex flex-col
      items-center mt-4 mr-4 text-white text-sm z-50"
      on:click={handleClose}>
      <CloseIcon />
      <span class="text-sm">(Esc)</span>
    </div>

    <!-- Add margin if you want to see some of the overlay behind the modal-->
    <div class="modal-content py-4 text-left px-6">
      <!--Title-->
      <div class="flex justify-between items-center pb-3">
        <p class="text-2xl font-bold">Filter</p>
        <div class="modal-close cursor-pointer z-50" on:click={handleClose}>
          <CloseIcon />
        </div>
      </div>

      <!--Body-->
      <slot />

      <!--Footer-->
      <div class="flex justify-end pt-2">
        <button
          class="px-4 bg-transparent p-3 rounded-lg text-indigo-500
          hover:bg-gray-100 hover:text-indigo-400 mr-2"
          on:click={handleSubmit}>
          Action
        </button>
        <button
          class="modal-close px-4 bg-indigo-500 p-3 rounded-lg text-white
          hover:bg-indigo-400"
          on:click={handleClose}>
          Close
        </button>
      </div>
    </div>
  </div>
</div>
