<script>
  import { createEventDispatcher } from 'svelte';
  import { histogram } from 'd3-array';
  import { scaleLinear } from 'd3-scale';

  const dispatch = createEventDispatcher();

  export let values = [];
  export let bins = 20;
  export let height = 90;

  let width;

  $: histHeight = height - 20;

  let min, max, histData;
  let xScale, yScale;

  $: getMinMax(values, width);

  $: v1 = min;
  $: v2 = max;

  $: handleChange(v1, v2);

  function getMinMax(values, width) {
    min = Math.min(...values);
    max = Math.max(...values);
    histData = histogram().thresholds(bins)(values);
    xScale = scaleLinear()
      .domain([histData[0].x0, histData[histData.length - 1].x1])
      .range([0, width]);
    yScale = scaleLinear()
      .domain([0, Math.max(...histData.map(bar => bar.length))])
      .range([0, histHeight]);
  }

  function handleChange(v1, v2) {
    dispatch('change', v1 < v2 ? [v1, v2] : [v2, v1]);
  }
</script>

<style>
  .range-slider {
    position: relative;
    height: 20px;
  }

  .range {
    position: absolute;
    width: 100%;
    -webkit-appearance: none;
    background: linear-gradient(to right, rgb(176, 176, 176), gray) no-repeat 0
      45% / 100% 40%;
    cursor: pointer;
  }

  .range::-webkit-slider-thumb {
    position: relative;
    z-index: 2;

    -webkit-appearance: none;
    appearance: none;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: #f0f0f0;
    border: 1px solid #999;
    cursor: pointer;
  }

  .range::-moz-range-thumb {
    z-index: 2;
    transform: scale(1);
  }

  .range::-moz-range-track {
    border-color: transparent;
  }
</style>

<div bind:clientWidth={width}>
  {#if width}
    <svg {width} height={histHeight}>
      {#each histData as bar}
        <rect
          x={xScale(bar.x0)}
          y={histHeight - yScale(bar.length)}
          width={xScale(bar.x1 - bar.x0)}
          height={yScale(bar.length)}
          fill="rgb(31, 119, 180)" />
      {/each}
    </svg>
  {/if}
  <div class="range-slider">
    <input type="range" class="range" {min} {max} step="any" bind:value={v1} />
    <input type="range" class="range" {min} {max} step="any" bind:value={v2} />
  </div>
  <div>{v1.toFixed(2)}..{v2.toFixed(2)}</div>
</div>
