<script>
  import {
    createEventDispatcher,
    onMount,
    beforeUpdate,
    afterUpdate
  } from 'svelte';
  import mapboxgl from 'mapbox-gl';
  import 'mapbox-gl/dist/mapbox-gl.css';
  const dispatch = createEventDispatcher();

  export let apartments;
  export let center = [27.56187791, 53.902334];
  export let zoom = 10;

  let container;
  let map;

  onMount(() => {
    console.log('[*] Map mounted', apartments);

    map = new mapboxgl.Map({
      container,
      center,
      zoom,
      accessToken: 'MAPBOX_ACCESS_TOKEN',
      style: 'mapbox://styles/mapbox/streets-v9'
    });

    const geolocateControl = new mapboxgl.GeolocateControl({
      positionOptions: {
        enableHighAccuracy: true
      },
      trackUserLocation: true
    });
    map.addControl(geolocateControl);

    map.on('load', () => {
      map.addLayer({
        id: '3d-buildings',
        source: 'composite',
        'source-layer': 'building',
        filter: ['==', 'extrude', 'true'],
        type: 'fill-extrusion',
        minzoom: 15,
        paint: {
          'fill-extrusion-color': '#aaa',

          // use an 'interpolate' expression to add a smooth transition effect to the
          // buildings as the user zooms in
          'fill-extrusion-height': [
            'interpolate',
            ['linear'],
            ['zoom'],
            15,
            0,
            15.05,
            ['get', 'height']
          ],
          'fill-extrusion-base': [
            'interpolate',
            ['linear'],
            ['zoom'],
            15,
            0,
            15.05,
            ['get', 'min_height']
          ],
          'fill-extrusion-opacity': 0.6
        }
      });

      const apartmentsData = {
        type: 'FeatureCollection',
        features: apartments.map(apartment => ({
          type: 'Feature',
          properties: {
            url: apartment.url
          },
          geometry: {
            type: 'Point',
            coordinates: [apartment.lng, apartment.lat]
          }
        }))
      };

      map.addSource('hidden-apartments', {
        type: 'geojson',
        data: apartmentsData
      });

      map.addLayer({
        id: 'hidden-points',
        type: 'circle',
        source: 'hidden-apartments',
        paint: {
          'circle-radius': 0
        }
      });

      map.addSource('apartments', {
        type: 'geojson',
        data: apartmentsData,
        cluster: true,
        clusterMaxZoom: 14, // Max zoom to cluster points on
        clusterRadius: 50 // Radius of each cluster when clustering points (defaults to 50)
      });

      map.addLayer({
        id: 'clusters',
        type: 'circle',
        source: 'apartments',
        filter: ['has', 'point_count'],
        paint: {
          // Use step expressions (https://docs.mapbox.com/mapbox-gl-js/style-spec/#expressions-step)
          // with three steps to implement three types of circles:
          //   * Blue, 20px circles when point count is less than 100
          //   * Yellow, 30px circles when point count is between 100 and 750
          //   * Pink, 40px circles when point count is greater than or equal to 750
          'circle-color': [
            'step',
            ['get', 'point_count'],
            '#51bbd6',
            100,
            '#f1f075',
            750,
            '#f28cb1'
          ],
          'circle-radius': [
            'step',
            ['get', 'point_count'],
            20,
            100,
            30,
            750,
            40
          ]
        }
      });

      map.addLayer({
        id: 'cluster-count',
        type: 'symbol',
        source: 'apartments',
        filter: ['has', 'point_count'],
        layout: {
          'text-field': '{point_count_abbreviated}',
          'text-font': ['DIN Offc Pro Medium', 'Arial Unicode MS Bold'],
          'text-size': 12
        }
      });

      map.addLayer({
        id: 'unclustered-point',
        type: 'circle',
        source: 'apartments',
        filter: ['!', ['has', 'point_count']],
        paint: {
          'circle-color': '#11b4da',
          'circle-radius': 4,
          'circle-stroke-width': 1,
          'circle-stroke-color': '#fff'
        }
      });
    });

    map.on('moveend', event => {
      if (event.target.getLayer('hidden-points')) {
        dispatch(
          'visibleChange',
          event.target.queryRenderedFeatures({
            layers: ['hidden-points']
          })
        );
      }
    });

    return () => {
      map.remove();
    };
  });

  beforeUpdate((...args) => {
    console.log('beforeUpdate', {
      apartments,
      map
    });

    if (map && map.getSource('apartments')) {
      const apartmentsData = {
        type: 'FeatureCollection',
        features: apartments.map(apartment => ({
          type: 'Feature',
          properties: {
            url: apartment.url
          },
          geometry: {
            type: 'Point',
            coordinates: [apartment.lng, apartment.lat]
          }
        }))
      };

      map.getSource('apartments').setData(apartmentsData);
      map.getSource('hidden-apartments').setData(apartmentsData);

      map.setCenter(center).setZoom(zoom);
    }
  });
</script>

<style>
  .map {
    position: relative;
    height: 100%;
  }
</style>

<div bind:this={container} class="map">
  <slot />
</div>
