<script>
  import { Link } from 'svelte-routing';

  let open = false;

  function handleToggle() {
    open = !open;
  }

  function getProps({ href, isPartiallyCurrent, isCurrent }) {
    const isActive = href === '/' ? isCurrent : isPartiallyCurrent || isCurrent;

    return {
      class: `block px-4 py-1 md:p-2 lg:px-4${
        isActive ? ' text-purple-600' : ''
      }`
    };
  }
</script>

<style>
  .navbar {
    transition: all 330ms ease-out;
  }

  .navbar-open {
    transform: translateX(0%);
  }
  .navbar-close {
    transform: translateX(-100%);
  }
</style>

<button
  on:click={handleToggle}
  class="absolute top-0 z-10 text-black text-2xl p-2 font-bold"
>
  &#9776;
</button>
<nav
  class="navbar w-64 absolute overflow-x-scroll bg-gray-700 top-0 h-screen"
  class:navbar-open={open}
  class:navbar-close={!open}
>
  <div class="flex pr-2 justify-end">
    <button on:click={handleToggle} class="p-2 text-white text-xl font-bold">
      &#9747;
    </button>
  </div>
  <h1 class="text-xl text-center font-bold pt-5">Menu</h1>
  <ul class="list-none text-white text-center">
    <li class="my-8">
      <Link {getProps} to="/">Home</Link>
    </li>
    <li class="my-8">
      <Link {getProps} to="/analytics">Analytics</Link>
    </li>
    <li class="my-8">
      <Link {getProps} to="/calculator">Calculator</Link>
    </li>
  </ul>
</nav>
